body {
    margin: 0;
    background-color: whitesmoke;
}

.header-bar {
  height: 100px;
  display:flex;
  align-items: center;
  justify-content: center;
  background-color: lightpink;
  font-family: "Goudy Stout";
  color: #245;
}

.header-bar > img {
    height: 80%;
    margin-left: 10%;
}

.header-bar > p {
    font-family: "Goudy Stout";
    color: whitesmoke;
    font-size: 15px;
    margin-left: 60px;
}


button{
	background-color: lightgrey;
	cursor: pointer;
	font-weight: bold;
	border: none;
    font-family: VT323, monospace;
    font-size: 25px;
    letter-spacing: 1.5px;
}

button:hover {
	background-color: blue;
	color: white;

}

.image-api {
	position: relative;
    width: 90%;
    margin: auto;

}

.image-api > img {
    width: 100%;
}

.image-api > h2 {
    position: absolute;
    width: 80%;
    text-align: center;
    left: 50%;
    transform: translateX(-50%);
    margin: 15px 0;
    padding: 0 5px;
    font-family: impact, sans-serif;
    font-size: 2em;
    text-transform: uppercase;
    color: white;
    letter-spacing: 1px;
    text-shadow:
        2px 2px 0 #000,
        -2px -2px 0 #000,
        2px -2px 0 #000,
        -2px 2px 0 #000,
        0 2px 0 #000,
        2px 0 0 #000,
        0 -2px 0 #000,
        -2px 0 0 #000,
        2px 2px 5px #000;
}

.image-api > .bottom {
    bottom: 0;
}

.image-api > .top {
    top: 0;
}

.meme {
    width: 90%;
    margin: 20px auto;
    display: flex;
    justify-content: space-between;
}

.meme > input {
    width: 45%;
    height: 40px;
}

